// 连连看测试
<template>
  <Wrap>
    <!-- 准备 -->
    <game-ready v-if="!start" @click="start = true" />
    <!-- 开始测试 -->
    <div v-if="start">
      <div
        v-if="prepareTime < 0 && !timeOut"
        style="width: 350px; margin-bottom: 30px"
      >
        <p>
          倒计时 <span class="big-time">{{ totalTime - time }}</span> 秒
        </p>
        <el-progress
          :percentage="percentage"
          :format="() => ''"
          color="#dd9222"
        ></el-progress>
      </div>
      <div class="square-wrap">
        <div
          class="square"
          v-for="(item, index) in shuffleList"
          :key="index"
          @click="checkItem(item)"
          :class="[
            { 'square-checked': item.checked },
            { 'square-checked-false': currentResult == -1 && item.checked },
          ]"
        >
          {{ item.text }}
        </div>

        <div class="square-wrap-masking" v-if="prepareTime >= 0 && score < 12">
          {{ prepareTime }}
        </div>
        <div class="square-wrap-masking" v-if="score >= 12">哇！你真快！</div>
        <div class="square-wrap-masking" v-if="score < 12 && timeOut">
          时间到！
        </div>
      </div>
    </div>
  </Wrap>
</template>
<script>
import Wrap from "../../components/CoreWrap.vue";
import GameReady from "../../components/GameReady.vue";
Array.prototype.shuffle = function () {
  var array = this;
  var m = array.length,
    t,
    i;
  while (m) {
    i = Math.floor(Math.random() * m--);
    t = array[m];
    array[m] = array[i];
    array[i] = t;
  }
  return array;
};
export default {
  components: { Wrap, GameReady },
  watch: {
    // 点击开始按钮后会进入准备倒计时
    start(newValue) {
      if (newValue) {
        var timer;
        timer = setInterval(() => {
          this.prepareTime = this.prepareTime - 1;
          if (this.prepareTime < 0) {
            clearInterval(timer);
            this.timeKeeping();
          }
        }, 1000);
      }
    },
  },
  data() {
    return {
      // 是否点击了开始测试按钮  点击开始按钮后会进入准备倒计时
      start: false,
      // 测试已经进行的时间
      time: 0,
      // 定时器
      timer: null,
      // 测试总时间
      totalTime: 60,
      // 测试开始倒计时秒数
      prepareTime: 3,
      // 得分
      score: 0,
      // 数据
      list: [
        { text: "しゃ", partnerText: "シャ", checked: false },
        { text: "シャ", partnerText: "しゃ", checked: false },

        { text: "ちゅ", partnerText: "チュ", checked: false },
        { text: "チュ", partnerText: "ちゅ", checked: false },

        { text: "みょ", partnerText: "ミョ", checked: false },
        { text: "ミョ", partnerText: "みょ", checked: false },

        { text: "あ", partnerText: "ァ", checked: false },
        { text: "ァ", partnerText: "あ", checked: false },

        { text: "き", partnerText: "キ", checked: false },
        { text: "キ", partnerText: "き", checked: false },

        { text: "は", partnerText: "ハ", checked: false },
        { text: "ハ", partnerText: "は", checked: false },

        { text: "ね", partnerText: "ネ", checked: false },
        { text: "ネ", partnerText: "ね", checked: false },

        { text: "ぴゃ", partnerText: "ピャ", checked: false },
        { text: "ピャ", partnerText: "ぴゃ", checked: false },

        { text: "ぬ", partnerText: "ヌ", checked: false },
        { text: "ヌ", partnerText: "ぬ", checked: false },

        { text: "い", partnerText: "イ", checked: false },
        { text: "イ", partnerText: "い", checked: false },

        { text: "う", partnerText: "ウ", checked: false },
        { text: "ウ", partnerText: "う", checked: false },

        { text: "れ", partnerText: "レ", checked: false },
        { text: "レ", partnerText: "れ", checked: false },
      ],
      // 当前被选中的方块数组
      currentCheckedArr: [],
      // 当前选中两个方块后的结果
      currentResult: 0, //0 没结果  -1错  1对
    };
  },
  computed: {
    // 进度条进度
    percentage() {
      return (this.time * 100) / this.totalTime;
    },
    // 打乱后的题目数据
    shuffleList() {
      let arr = this.list.shuffle();
      arr.splice(12, 0, { text: "" });
      return arr;
    },
    // 是否到时间了
    timeOut() {
      return this.totalTime - this.time < 0 ? true : false;
    },
  },
  methods: {
    // 测试计时
    timeKeeping() {
      this.timer = setInterval(() => {
        this.time = this.time + 1;
        if (this.time > this.totalTime) {
          this.clearTimer();
        }
      }, 1000);
    },
    // 清除测试计时
    clearTimer() {
      clearInterval(this.timer);
    },
    // 点击方块时
    checkItem(item) {
      if (!item.text) {
        return;
      }
      item.checked = !item.checked;
      if (item.checked) {
        this.currentCheckedArr.push(item);
      } else {
        this.currentCheckedArr = [];
      }
      if (this.currentCheckedArr.length == 2) {
        if (
          this.currentCheckedArr[0].text ==
          this.currentCheckedArr[1].partnerText
        ) {
          this.currentCheckedArr[0].checked = false;
          this.currentCheckedArr[1].checked = false;
          this.currentCheckedArr[0].text = "";
          this.currentCheckedArr[1].text = "";
          this.currentCheckedArr = [];
          this.currentResult = 0;
          this.score = this.score + 1;
        } else {
          this.currentResult = -1;
          setTimeout(() => {
            this.currentCheckedArr[0].checked = false;
            this.currentCheckedArr[1].checked = false;
            this.currentResult = 0;
            this.currentCheckedArr = [];
          }, 1000);
        }
      } else {
        this.currentResult = 0;
      }
      if (this.score == 12) {
        this.clearTimer();
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.big-time {
  color: $self-orange;
  font-size: 36px;
  font-weight: 600;
}
.square-wrap {
  position: relative;
  max-width: 271px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &-masking {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.9);
    color: $self-orange;
    font-size: 34px;
    font-weight: 600;
    text-align: center;
    line-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.square {
  width: 50px;
  height: 50px;
  background-color: pink;
  border-radius: 5px;
  color: #fff;
  margin-right: 5px;
  margin-bottom: 5px;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  &:nth-child(2n) {
    background-color: $self-square-color;
  }
  &:nth-child(2n + 1) {
    background-color: $self-blue;
  }
  &:nth-child(13) {
    background-color: $self-checked-color;
    background: url("../../assets/小型猫窝.png") no-repeat center;
    border: 2px solid $self-square-color;
  }
  &:nth-child(5n) {
    margin-right: 0;
  }
  &-checked {
    background-color: $self-orange !important;
    // animation: blink 1s;
  }
  &-checked-false {
    animation: blink 1s;
  }
}

@keyframes blink {
  0% {
    box-shadow: none;
  }
  25% {
    box-shadow: 0px 0px 2px $self-orange;
  }
  50% {
    box-shadow: 0px 0px 4px $self-orange;
  }
  75% {
    box-shadow: 0px 0px 2px $self-orange;
  }
  100% {
    box-shadow: none;
  }
}
</style>